<template>
  <div class="part-manage">
    <div class="button-group">
      <span class="route-name">{{$route.meta.title}}</span>
      <el-button type="warning" size="small" @click="handleAdd()" icon="el-icon-plus">添加</el-button>
    </div>
    <tree-table v-loading="tableLoading" :columns="columns" :data-source="deptDataList" :tree-handel="treeHandel" :expand-place="1">
      <template slot-scope="scope">
        <el-dropdown trigger="click" placement="bottom">
          <el-button type="text" size="mini">
            操作
            <i class="el-icon-arrow-down el-icon--right"></i>
          </el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>
              <el-button size="mini" type="text" icon="el-icon-edit" @click="handleEdit(scope.row)">编辑</el-button>
            </el-dropdown-item>
            <el-dropdown-item>
              <el-button size="mini" type="text" icon="el-icon-circle-plus-outline" @click="handleAdd(scope.row)">添加下级</el-button>
            </el-dropdown-item>
            <el-dropdown-item divided>
              <el-button size="mini" type="text" class="btn-text-red" icon="el-icon-delete" @click="handleDelete(scope.row)">删除</el-button>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </template>
    </tree-table>
    <!-- 操作弹框 -->
    <el-dialog :title="handleTitle" :visible.sync="handleVisible" width="30%" center>
      <el-form ref="deptForm" :model="deptForm" status-icon label-width="90px" size="small">
        <el-form-item label="上级部门">
          <el-row>
            <el-col :span="24">
              <el-input v-model="deptForm.parent_name" type="text" disabled></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="部门名称">
          <el-row>
            <el-col :span="24">
              <el-input v-model.trim="deptForm.name" type="text"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="排序">
          <el-row>
            <el-col :span="24">
              <el-input v-model.trim="deptForm.order_num" type="text"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="状态">
          <el-row>
            <el-col :span="24">
              <el-radio v-model.trim="deptForm.del_flag" label="0">正常</el-radio>
              <el-radio v-model.trim="deptForm.del_flag" label="1">禁用</el-radio>
            </el-col>
          </el-row>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" type="primary" @click="handleConfirm">确 定</el-button>
        <el-button size="small" @click="handleVisible = false">取 消</el-button>
      </span>
    </el-dialog>
    <!-- 删除弹窗 -->
    <el-dialog title="删除部门" :visible.sync="delVisible" width="260px" center>
      <div class="del-dialog-cnt">删除不可恢复，是否确定删除？</div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" type="danger" @click="deleteRow">确 定</el-button>
        <el-button size="small" @click="delVisible = false">取 消</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'

export default {
  data() {
    return {
      tableLoading: true,
      handleTitle: '',
      handleVisible: false,
      delVisible: false,
      columns: [
        {
          text: '编号',
          dataIndex: 'id',
          width: 70
        },
        {
          text: '部门名称',
          dataIndex: 'text'
        },
        {
          text: '排序',
          dataIndex: 'order_num'
        },
        {
          text: '状态',
          dataIndex: 'del_flag',
          type: 'status'
        }
      ],
      treeHandel: {
        text: '系统操作',
        width: 160
      },
      deptForm: {
        parent_name: '',
        parent_id: '', // 上级部门id，如果没有上级部门就填写0
        name: '',
        order_num: '',
        del_flag: '0'
      }
    }
  },
  computed: {
    ...mapState({
      deptDataList: state => state.authority.deptDataList
    })
  },
  mounted() {
    this.getData()
  },
  methods: {
    ...mapActions('authority', ['getDeptList', 'addNewDept', 'editDept', 'removeDept', 'deptListById']),
    getData() {
      const vm = this
      vm.getDeptList(vm)
    },
    resetForm() {
      this.deptForm = {
        parent_name: '总部门',
        parent_id: 0,
        name: '',
        order_num: '',
        del_flag: '0'
      }
    },
    handleAdd(row) {
      this.resetForm()
      this.handleTitle = '添加新部门'
      this.handleVisible = true
      if (row) {
        this.deptForm.parent_name = row.text
        this.deptForm.parent_id = row.id
      }
    },
    async handleEdit(row) {
      const vm = this
      vm.handleTitle = '修改部门'
      vm.handleVisible = true
      let parentName = ''
      if (row.hasParent) {
        vm.deptForm.deptById = row.parentId
        parentName = await vm.deptListById(vm)
      } else {
        parentName = '无'
      }
      vm.deptForm = {
        dept_id: row.attributes.id,
        parent_name: parentName,
        parent_id: row.parentId,
        name: row.attributes.text,
        order_num: row.attributes.order_num,
        del_flag: row.attributes.del_flag.toString()
      }
    },
    handleDelete(row) {
      this.delVisible = true
      this.deptForm.dept_id = row.attributes.id
    },
    handleConfirm() {
      const vm = this
      if (vm.deptForm.name === '') {
        vm.$message({
          type: 'warning',
          message: '部门名称不为空'
        })
        return
      }

      if (vm.handleTitle === '添加新部门') {
        vm.addNewDept(vm)
      }
      if (vm.handleTitle === '修改部门') {
        vm.editDept(vm)
      }
      vm.handleVisible = false
    },
    deleteRow() {
      this.removeDept(this)
      this.delVisible = false
    }
  }
}
</script>

<style type="text/scss" lang="scss" scoped>
.part-manage {
}
</style>



